.lesson {
  background-color: $second-bg-color;
  padding-top: 24px;
  padding-bottom: 24px;

  @include mq(tablet) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @include mq(tablet) {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .container {
    @include mq(desktop) {
      display: flex;
      align-items: center;
      justify-content: space-around;
    }
  }

  &-title {
    position: relative;
    @extend %section-title;
    margin-bottom: 16px;
    width: 250px;

    @include mq(tablet) {
      text-align: center;
      margin: 0 auto;
      margin-bottom: 124px;
      width: 572px;
    }

    @include mq(desktop) {
      margin: 0;
      margin-bottom: 124px;
      width: 490px;
    }



    &::after {
      @include mq(tablet) {
        content: '';
        background-image: url('../images/lesson/lesson-lines-right.svg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        bottom: -90px;
        top: 10px;
        right: -75px;
        width: 45px;
        height: 85px;
      }

      @include mq(desktop) {
        top: -25px;
        right: -685px;
        width: 65px;
        height: 130px;
      }
    }

    &::before {
      @include mq(tablet) {
        content: '';
        background-image: url('../images/lesson/lesson-lines-left.svg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        top: 10px;
        left: -50px;
        width: 45px;
        height: 85px;
      }

      @include mq(desktop) {
        top: -25px;
        left: -95px;
        width: 65px;
        height: 130px;
      }
    }
  }

  &-title__span {
    text-transform: uppercase;
    position: relative;
    z-index: 2;

    &::before {
      z-index: -1;
      content: '';
      background-image: url('../images/lesson/span-accent.svg');
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      top: 6px;
      left: 3px;

      width: 140px;
      height: 16px;

      @include mq(tablet) {
        width: 328px;
        height: 40px;

        top: 15px;
        left: 10px;
      }
    }

    &::after {
      @include mq(tablet) {
        content: '';
        background-image: url('../images/lesson/lesson-title-wave.svg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        bottom: -130px;
        left: -230px;
        width: 579px;
        height: 72px;
      }

      @include mq(desktop) {
        width: 595px;
        bottom: -160px;
        left: -260px;
      }
    }
  }

  &-form {
    text-align: center;

    @include mq(tablet) {
      background-color: $form-bg-color;
      margin: 0 auto;
      max-width: 570px;
      padding: 36px;
    }

    @include mq(desktop) {
      max-width: 500px;
      margin: 0;
    }

    input {
      width: 100%;
      padding-top: 12px;
      padding-bottom: 12px;
      padding-left: 24px;
      border-radius: 0;

      border: none;
      padding: 10px 24px 10px 24px;

      margin-bottom: 24px;

      outline: none;

      &::placeholder {
        font-weight: 300;
        font-size: 14px;
        line-height: 0.92;
        color: $second-text-color;


        @include mq(tablet) {
          font-size: 18px;
        }
      }

      @include mq(tablet) {
        padding-top: 23px;
        padding-bottom: 23px;
        padding: 26px 56px 26px 56px;
      }
    }

    button {
      position: relative;
      cursor: pointer;
      background-color: $btn-bg-color;
      color: $btn-text-color;
      font-weight: 700;
      font-size: 14px;
      line-height: 0.92;
      width: 184px;
      padding-top: 14px;
      padding-bottom: 14px;

      transition: border 150ms linear, background 150ms linear;

      @include mq(tablet) {
        width: 100%;
        padding-top: 24px;
        padding-bottom: 24px;
        border: 1px solid $btn-bg-color;
      }

      &:hover,
      &:focus {
        border-color: $accent-color;
        background-color: $accent-color;
      }

      @include mq(mobile-only) {
        &::after {
          content: '';
          background-image: url('../images/lesson/lesson-lines.svg');
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center;
          position: absolute;

          top: -15px;
          right: -45px;

          width: 37px;
          height: 70px;
        }
      }
    }
  }
}
