.hero {
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;

  background-image: url('../images/hero/mobile/hero-background.svg'), linear-gradient(167.86deg, rgba(249, 222, 137, 0.57) 13.91%, rgba(254, 200, 48, 0) 87.72%, rgba(255, 255, 255, 0.11) 119.62%);

  background-position: left -2px top, center;
  background-repeat: no-repeat;
  background-size: auto;

  @include mq(tablet) {
    padding-top: 62px;
    padding-bottom: 43px;

    background-image: url('../images/hero/tablet/hero-background.svg'), url('../images/hero/title-wave-tablet.svg'), linear-gradient(167.86deg, rgba(249, 222, 137, 0.57) 13.91%, rgba(254, 200, 48, 0) 87.72%, rgba(255, 255, 255, 0.11) 119.62%);
    background-position: left -20px bottom, 8% 12%, 100%;
    background-size: auto, 60px;
  }

  @include mq(desktop) {
    padding-top: 64px;
    padding-bottom: 120px;

    background-image:
      url('../images/hero/desktop/hero-background.svg'),
      url('../images/hero/title-wave-desktop.svg'),
      linear-gradient(167.86deg, rgba(249, 222, 137, 0.57) 13.91%,
        rgba(254, 200, 48, 0) 87.72%,
        rgba(255, 255, 255, 0.11) 119.62%);

    background-position: left top, 10% 2%, 100%;

    background-size: auto, 200px;
  }

  &-suptitle {
    text-align: center;
    @extend %section-title;
    font-weight: 700;
    font-size: 24px;
    line-height: 1;
    margin-bottom: 8px;

    @include mq(tablet) {
      font-size: 40px;
      line-height: 1.5;
      margin-bottom: 28px;
    }
  }

  &-title--large {
    @extend %section-title;
    max-width: 276px;
    text-align: center;
    margin: 0 auto;
    font-size: 40px;
    line-height: 1.05;
    margin-bottom: 20px;

    @include mq(tablet) {
      position: relative;
      font-size: 60px;
      line-height: 0.95;
      max-width: 100%;
      margin-bottom: 48px;
    }

    @include mq(desktop) {
      font-size: 96px;
      margin-bottom: 136px;
    }
  }

  &__content {
    h2 {
      @extend %section-title;
      margin-bottom: 16px;

      @include mq(tablet) {
        margin-bottom: 28px;
      }

      @include mq(desktop) {
        font-size: 64px;
      }
    }

    p {
      @extend %section-text;
      line-height: 1.45;

      @include mq(tablet) {
        font-size: 16px;
        line-height: 1.2;
        max-width: 420px;
      }

      @include mq(desktop) {
        font-size: 24px;
        line-height: 1.2;
        max-width: 630px;
      }
    }

    p:not(:last-child) {
      margin: 0 0 30px 0;
    }
  }



  &-image {
    display: none;

    @include mq(tablet) {
      display: block;
      position: absolute;
      bottom: 20px;
      left: 68%;
    }

    @include mq(desktop) {
      width: 512px;
      left: 60%;
      bottom: 5%;
    }
  }
}
