.subscribe {
  padding-top: 24px;
  padding-bottom: 24px;
  background-color: $second-bg-color;

  .container {
    @include mq(desktop) {
      display: flex;
      align-items: flex-start;
      justify-content: center;
    }
  }

  @include mq(mobile-only) {
    background-image: url('..images/subscribe/wave-mobile.svg');

    background-size: 100%;
    background-repeat: no-repeat;

    background-position: bottom -20px right 0;
  }

  @include mq(desktop) {
    background-image: url('..images/subscribe/wave-desktop.svg');

    background-size: 100%;
    background-repeat: no-repeat;

    background-position: bottom, left;
  }

  @include mq(tablet) {
    padding-top: 40px;
    padding-bottom: 40px
  }

  @include mq(desktop) {
    padding-top: 68px;
    padding-bottom: 68px
  }

  &-title {
    @extend %section-title;
    text-align: left;
    max-width: 260px;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    margin-bottom: 16px;

    @include mq(tablet) {
      text-align: center;
      max-width: 500px;
      margin: 0 auto;
      margin-bottom: 32px;
      font-size: 30px;
      line-height: 1.73;
    }

    @include mq(desktop) {
      margin: 0;
    }
  }

  &-text__span {
    font-weight: 700;
  }

  &-form__box {
    @include mq(tablet) {
      display: flex;
      justify-content: center;
      margin-bottom: 16px;
    }

    input {
      width: 100%;
      padding-top: 12px;
      padding-bottom: 12px;
      padding-left: 24px;
      outline: none;
      margin-bottom: 24px;
      border-radius: 0;
      border: none;

      &::placeholder {
        font-size: 14px;
        line-height: 0.92;
        color: $second-text-color;
        font-weight: 300;

        @include mq(tablet) {
          font-size: 18px;
        }
      }

      @include mq(tablet) {
        padding-top: 23px;
        padding-bottom: 23px;
        padding-left: 52px;
        width: 300px;
        margin-bottom: 0;
      }
    }

    button {
      cursor: pointer;
      background-color: $btn-bg-color;
      color: $btn-text-color;
      font-weight: 700;
      font-size: 14px;
      line-height: 0.92;
      width: 184px;
      padding-top: 14px;
      padding-bottom: 14px;

      display: block;
      margin: 0 auto;
      margin-bottom: 12px;

      transition: border 150ms linear, background 150ms linear;

      @include mq(tablet) {
        padding-top: 24px;
        padding-bottom: 24px;
        width: 265px;
        border: 1px solid $btn-bg-color;
        min-width: 270px;
        margin: 0;
      }

      &:hover,
      &:focus {
        border-color: $accent-color;
        background-color: $accent-color;
      }
    }
  }

  &-form__wrapper {
    position: relative;
  }

  &-form::before {
    content: '';
    background-image: url('../images/subscribe/lines-left.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 12px;
    left: -25px;

    width: 25px;
    height: 25px;

    @include mq(tablet) {
      top: -150px;

      width: 90px;
      height: 130px;
    }

    @include mq(desktop) {
      top: -15px;
      left: -575px;
    }
  }

  &-form::after {
    content: '';
    background-image: url('../images/subscribe/lines-right.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 12px;
    right: -25px;

    width: 25px;
    height: 25px;

    @include mq(tablet) {
      top: -150px;
      right: -20px;
      width: 75px;
      height: 130px;
    }

    @include mq(desktop) {
      top: -25px;
      right: -90px;
    }
  }



  &-form__label--padding {
    @extend %section-text;
    position: relative;
    display: inline-block;
    min-width: 200px;
    padding-left: 30px;

    line-height: 1.2;
    font-weight: 500;
    text-align: left;
    color: $third-bg-color;

    @include mq(tablet) {
      font-size: 18px;
      line-height: 1.4;
      padding-left: 50px;
      margin-left: 65px;
    }

    @include mq(desktop) {
      margin-left: 0;
    }
  }

  &-form__checkbox {
    position: absolute;
    width: 18px;
    height: 18px;
    left: 0;
    top: 0;

    @include mq(tablet) {
      top: 2px;
    }
  }
}
