.country-select {
  position: relative;
  display: block;
}
.country-select * {
  box-sizing: border-box;
}
.country-select .hide {
  display: none;
}
.country-select .v-hide {
  visibility: hidden;
}
.country-select input,
.country-select input[type='text'] {
  position: relative;
  z-index: 0;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  padding-right: 36px;
  margin-right: 0;
}
.country-select .flag-dropdown {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  padding: 1px;
}
.country-select .selected-flag {
  z-index: 1;
  position: relative;
  width: 36px;
  height: 100%;
  padding: 0 0 0 8px;
}
.country-select .selected-flag .flag {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
}
.country-select .selected-flag .arrow {
  position: absolute;
  top: 50%;
  margin-top: -2px;
  right: 6px;
  width: 0;
  height: 0;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 4px solid #555;
}
.country-select .selected-flag .arrow.up {
  border-top: none;
  border-bottom: 4px solid #555;
}
.country-select .country-list {
  position: absolute;
  z-index: 2;
  list-style: none;
  text-align: left;
  padding: 0;
  margin: 0 0 0 -1px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  background-color: white;
  border: 1px solid #ccc;
  color: black;
  white-space: nowrap;
  max-height: 200px;
  overflow-y: scroll;
}
.country-select .country-list .flag {
  display: inline-block;
  width: 20px;
}
@media (max-width: 500px) {
  .country-select .country-list {
    white-space: normal;
  }
}
.country-select .country-list .divider {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid #ccc;
}
.country-select .country-list .country {
  padding: 5px 10px;
}
.country-select .country-list .country.highlight {
  background-color: rgba(0, 0, 0, 0.05);
}
.country-select .country-list .flag,
.country-select .country-list .country-name {
  vertical-align: middle;
}
.country-select .country-list .flag {
  margin-right: 6px;
}
.country-select.inside input,
.country-select.inside input[type='text'] {
  padding-right: 6px;
  padding-left: 52px;
  margin-left: 0;
}
.country-select.inside .flag-dropdown {
  right: auto;
  left: 0;
}
.country-select.inside .selected-flag {
  width: 46px;
}
.country-select.inside .flag-dropdown:hover {
  cursor: pointer;
}
.country-select.inside .flag-dropdown:hover .selected-flag {
  background-color: rgba(0, 0, 0, 0.05);
}
.country-select.inside input[disabled] + .flag-dropdown:hover,
.country-select.inside input[readonly] + .flag-dropdown:hover {
  cursor: default;
}
.country-select.inside input[disabled] + .flag-dropdown:hover .selected-flag,
.country-select.inside input[readonly] + .flag-dropdown:hover .selected-flag {
  background-color: transparent;
}
.country-select .flag {
  width: 20px;
  height: 15px;
  box-shadow: 0px 0px 1px 0px #888;
  background-image: url('../images/countrySelect/flags.png');
  background-image: -webkit-image-set(url('../images/countrySelect/flags.webp') 1x);
  background-repeat: no-repeat;
  background-color: #dbdbdb;
  background-position: 20px 0;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (min--moz-device-pixel-ratio: 2),
  only screen and (min-device-pixel-ratio: 2),
  only screen and (min-resolution: 192dpi),
  only screen and (min-resolution: 2dppx) {
  .country-select .flag {
    background-image: url('../images/countrySelect/flags@2x.png');
    background-image: -webkit-image-set(url('../images/countrySelect/flags@2x.webp') 2x);
  }
}
