.publication {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: $first-bg-color;

  background-image:
    url('../images/publication/background/publication-background-star-1.svg'),
    url('../images/publication/background/publication-background-rocket-1.svg'),
    url('../images/publication/background/publication-background-star-2.svg'),
    url('../images/publication/background/publication-background-rocket-2.svg'),
    url('../images/publication/background/publication-background-star-3.svg'),
    url('../images/publication/background/publication-background-rocket-3.svg');

  background-repeat: no-repeat;

  background-position:
    top 10% left -10%,
    top 20% right -10%,
    top 45% left -10%,
    top 58% right -10%,
    bottom 18% left -10%,
    bottom 5% right -10%;

  background-size: 225px 296px;


  @include mq(tablet) {
    background-image:
      url('../images/publication/background/publication-background-star-1.svg'),
      url('../images/publication/background/publication-background-rocket-1.svg'),
      url('../images/publication/background/publication-background-rocket-2.svg'),
      url('../images/publication/background/publication-background-star-2.svg'),
      url('../images/publication/background/publication-background-rocket-3.svg');

    background-size: 400px 296px;
  }

  @include mq(desktop) {
    background-image:
      url('../images/publication/background/publication-background-star-1.svg'),
      url('../images/publication/background/publication-background-rocket-1.svg');
    background-position:
      bottom 10% left -6%,
      top 6% right -6%;
    background-size: 400px 296px;
  }

  &-title {
    @extend %section-title;
    margin-bottom: 20px;
    letter-spacing: 0.06em;

    @include mq(tablet) {
      margin-bottom: 36px;
      letter-spacing: 0em;
    }
  }

  &-list {
    @include mq(desktop) {
      display: flex;
      flex-wrap: wrap;
      gap: 30px;
    }
  }

  li {
    position: relative;
    margin: 0 auto;
    max-width: 280px;


    @include mq(tablet) {
      min-width: 374px;
    }

    @include mq(desktop) {
      margin: 0;
      min-width: 370px;
    }

    &:not(last-child) {
      margin-bottom: 40px;

      @include mq(tablet) {
        margin-bottom: 32px;
      }

      @include mq(desktop) {
        margin-bottom: 0;
      }
    }
  }

  &-image {
    @extend %image;
    width: 100%
  }

  &-label {
    @extend %section-text;
    text-align: center;
    min-width: 122px;
    position: absolute;
    top: 12px;
    left: 16px;
    display: inline-block;
    background-color: $third-bg-color;
    border-radius: 15px;
    padding: 8px 15px;
    color: $third--text-color;
    font-weight: 500;

    @include mq(tablet) {
      top: 24px;
      left: 24px;
      font-size: 16px;
      line-height: 1.25;
      letter-spacing: -0.02em;
      min-width: 188px;
    }
  }

  &-description {
    background-color: $third-bg-color;
    padding: 20px 20px 30px 20px;

    @include mq(tablet) {
      min-height: 372px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 40px 20px 36px;
    }
  }

  &-description__title {
    @extend %section-title;
    max-width: 195px;
    line-height: 1.1;

    @include mq(mobile-only) {
      margin-bottom: 8px;
    }

    @include mq(tablet) {
      font-size: 22px;
      line-height: 1.45;
      max-width: 320px;
    }
  }

  &-description__text {
    @extend %section-text;
    max-width: 215px;


    @include mq(mobile-only) {
      margin-bottom: 16px;
    }

    @include mq(tablet) {
      font-size: 18px;
      line-height: 1.33;
      max-width: 300px;
    }
  }

  &-description__link {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.79;
    color: $third--text-color;

    @include mq(tablet) {
      font-size: 18px;
      line-height: 1.39;
    }
  }
}
