/* BACKGROUND COLOR */
$first-bg-color: #fffcf2;
$second-bg-color: #F9DE8A;
$third-bg-color: #ffffff;
$form-bg-color: #FDF3D4;
$btn-bg-color: #E03EAB;

/* TEXT COLOR */
$title-color: #272D4E;
$main-text-color: #707882;
$second-text-color: #272D4E;
$third--text-color: #A04CA1;
$btn-text-color: #ffffff;

/* ACCENT COLOR */
$accent-color: #C51D8E;
$seccond-accent-color: #edc1dd;

/* MODAL COLOR */
$modal-text: #000000;
$modal-accent: #ff6c00;
$modal-white: #ffffff;

/* ANIMATIONS SPEED */
$fast: 0.3s;
$slow: 0.5s;

/* PLACEHOLDERS */
%section-title {

  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 0.90;
  color: $title-color;

  @include mq(tablet) {
    font-size: 48px;
    line-height: 0.95;

  }
}

%section-text {

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: $main-text-color;
}

%image {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}
