  .find {
    padding-top: 20px;
    padding-bottom: 24px;

    @include mq(tablet) {
      padding-top: 48px;
      padding-bottom: 40px;
    }

    &-form {
      text-align: center;
      margin-bottom: 40px;

      @include mq(desktop) {
        display: flex;
        justify-content: space-between;
        gap: 23px;
      }

      input {
        width: 100%;
        padding-top: 12px;
        padding-bottom: 12px;
        padding-left: 24px;
        margin-bottom: 12px;
        border: 1px solid #272D4E;
        outline: none;
        border-radius: 0;

        @include mq(tablet) {
          max-width: 206px;
          margin-bottom: 0;
        }

        &::placeholder {
          font-weight: 300;
          font-size: 14px;
          line-height: 0.92;
          color: $second-text-color;

          @include mq(tablet) {
            font-size: 18px;
          }

          @include mq(desktop) {
            font-size: 24px;
            line-height: 0.91;
            letter-spacing: -0.02em;
          }
        }

        @include mq(desktop) {
          padding-top: 23px;
          padding-bottom: 23px;
        }
      }

      select {
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;


        background-image: url('../images/find/find-select-arrow.svg');
        background-repeat: no-repeat;
        background-position: right 24px top 50%;
        background-size: 24px auto;
        background-color: $third-bg-color;

        width: 100%;
        padding-top: 12px;
        padding-bottom: 12px;
        padding-left: 24px;
        margin-bottom: 12px;
        border: 1px solid #272D4E;
        outline: none;

        @include mq(tablet) {
          max-width: 307px;
          margin-bottom: 0;
        }

        &:valid {
          font-weight: 300;
          font-size: 14px;
          color: $second-text-color;

          @include mq(tablet) {
            font-size: 18px;
          }

          @include mq(desktop) {
            font-size: 24px;
            line-height: 0.91;
            letter-spacing: -0.02em;
          }
        }
      }

      label {
        display: block;
        width: 100%;

        &:nth-child(1) {
          margin-bottom: 24px;
        }

        @include mq(tablet) {
          display: flex;

          &:nth-child(1) {
            margin-bottom: 40px;

            @include mq(desktop) {
              margin-bottom: 0;
            }
          }
        }
      }

      button {
        cursor: pointer;
        background-color: $btn-bg-color;
        color: $btn-text-color;
        font-weight: 700;
        font-size: 14px;
        line-height: 0.92;
        width: 184px;
        padding-top: 14px;
        padding-bottom: 14px;

        transition: border 150ms linear, background 150ms linear;

        @include mq(tablet) {
          padding-top: 24px;
          padding-bottom: 24px;
          width: 265px;
          border: 1px solid $btn-bg-color;
        }

        &:hover,
        &:focus {
          border-color: $accent-color;
          background-color: $accent-color;
        }
      }
    }

    &-wrapper__text {
      font-size: 20px;
      line-height: 19px;
      color: $second-text-color;
      margin-bottom: 16px;

      @include mq(tablet) {
        font-size: 24px;
        line-height: 0.95;
      }

      @include mq(desktop) {
        font-size: 30px;
        line-height: 0.96;

        margin-bottom: 0;
      }
    }

    &-wrapper {
      @include mq(desktop) {
        display: flex;
        align-items: center;
        gap: 66px;
      }
    }

    &-wrapper__list {
      display: flex;
      flex-wrap: wrap;
      // gap: 20px;
      row-gap: 12px;
      font-size: 14px;
      line-height: 1.43;
      font-weight: 500;
      color: $second-text-color;

      @include mq(mobile-only) {
        max-width: 280px;
      }

      @include mq(tablet) {
        width: 500px;
        gap: 16px;
        row-gap: 20px;

        font-size: 18px;
        line-height: 17px;
      }

      @include mq(desktop) {
        flex-wrap: nowrap;
        gap: 8px;
        width: auto;
      }

      li {

        min-width: 120px;

        &:nth-child(1) {

          @include mq(mobile-only) {
            margin-right: 16px;
          }

          @include mq(tablet) {
            min-width: 204px;
          }
        }

        &:nth-child(2) {
          @include mq(tablet) {
            min-width: 204px;
          }
        }

        &:nth-child(3) {

          @include mq(mobile-only) {
            margin-right: 15px;
            min-width: 130px;
          }

          @include mq(tablet) {
            min-width: 204px;
          }
        }

        &:nth-child(4) {
          @include mq(tablet) {
            min-width: 231px;
          }
        }
      }
    }

    &-wrapper__link {
      display: block;
      text-align: center;
      background-color: #FFEBF8;
      border-radius: 20px;
      padding: 8px;

      transition: background 150ms linear;

      @include mq(tablet) {
        padding: 16px;
        border-radius: 50px;
      }

      &:hover,
      &:focus {
        background-color: $seccond-accent-color;
      }
    }
  }
