.offers {
  background-color: $first-bg-color;

  padding-top: 40px;
  padding-bottom: 40px;

  @include mq(tablet-only) {
    background-image:
      url('../images/offers/background/offers-wave.svg'),
      url('../images/offers/background/offers-rocket.svg'),
      url('../images/offers/background/offers-lightning.svg');

    background-repeat: no-repeat;

    background-size: 100%, auto, auto;

    background-position:
      top 41% center,
      bottom 10% left -5%,
      bottom 10% right 10%,
  }

  @include mq(desktop) {
    background-image:
      url('../images/offers/background/offers-wave-desktop.svg'),
      url('../images/offers/background/offers-lightning-desktop.svg');

    background-repeat: no-repeat;

    background-size: 100%, auto;

    background-position:
      center,
      top 10% right 5%,
  }



  @include mq(tablet) {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &-title {
    margin: 0 auto;
    @extend %section-title;
    line-height: 1.1;
    max-width: 263px;
    margin-bottom: 20px;


    @include mq(tablet) {
      max-width: 595px;
      margin-bottom: 36px;
      line-height: 1.17;
    }

    @include mq(desktop) {
      margin: 0;
      margin-bottom: 55px;
      max-width: 875px;
    }
  }

  &-list {
    @include mq(tablet) {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      row-gap: 25px
    }

    li {
      margin-bottom: 20px;

      @include mq(tablet) {
        margin-right: 20px;
        margin-bottom: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &-image {
    margin: 0 auto;
    height: 100%;

    @extend %image;

    @include mq(tablet) {
      width: 275px;

    }

    @include mq(desktop) {
      width: 370px;
    }
  }
}
